import { Box, Button } from "@chakra-ui/react";
import { useEffect, useState } from "react";
import { Result, resultbox } from "./Result";

export const QnA = ({ page, setPage, colorMode, name }) => {
  const questionList = [
    {
      q: [`     .`],
      a: [
        { text: "  ...   ~ ", score: 0 },
        { text: "?    ", score: 1 },
        { text: " !   ...", score: 2 },
        { text: "   ? ", score: 3 },
      ],
    },
    {
      q: ["      ."],
      a: [
        { text: "?   ? ", score: 0 },
        { text: " ...  ! ", score: 1 },
        { text: "  ...", score: 2 },
        { text: "    ...!", score: 3 },
      ],
    },
    {
      q: ["         ."],
      a: [
        { text: "!    !", score: 0 },
        { text: "  ,   !", score: 1 },
        { text: "  ...     ?", score: 2 },
        { text: "   ...?    !", score: 3 },
      ],
    },
    {
      q: ["       ."],
      a: [
        { text: "    .  ", score: 0 },
        { text: " ,  ", score: 1 },
        { text: "   ...!", score: 2 },
        { text: "     ,  ~!", score: 3 },
      ],
    },
    {
      q: ["     ."],
      a: [
        { text: "   ~!   ", score: 0 },
        { text: "...   ", score: 1 },
        { text: "      ", score: 2 },
        { text: "   !", score: 3 },
      ],
    },
    {
      q: ["        ."],
      a: [
        { text: " ...    ?", score: 0 },
        { text: "?     !", score: 1 },
        { text: "   ;;", score: 2 },
        { text: "...   ...", score: 3 },
      ],
    },
    {
      q: ["      ."],
      a: [
        { text: "  ,  ", score: 0 },
        {
          text: "  !    ",
          score: 1,
        },
        { text: " ...     .", score: 2 },
        { text: " ...  .   ", score: 3 },
      ],
    },
    {
      q: ["      ."],
      a: [
        { text: "  !   ! ", score: 0 },
        { text: "?   !", score: 1 },
        { text: " ...   ? ", score: 2 },
        { text: "...   ?    ", score: 3 },
      ],
    },
    {
      q: ["      ."],
      a: [
        { text: "    !    ", score: 0 },
        { text: "   !    ", score: 1 },
        {
          text: "  ...      ?",
          score: 2,
        },
        {
          text: "  ...  ",
          score: 3,
        },
      ],
    },
    {
      q: ["     ."],
      a: [
        { text: " ,    !", score: 0 },
        {
          text: "       !",
          score: 1,
        },
        { text: "  ...  ", score: 2 },
        {
          text: " !    ",
          score: 3,
        },
      ],
    },

    {
      q: ["  :)"],
      a: [{ text: "!    ", score: 0 }],
    },
  ];

  const [counts, setCounts] = useState({ score: 0 });

  const handleClickAnswer = (score, idx) => {
    let value = counts;
    value.score = value.score + score;

    setCounts(value);
    setPage(page + 1);

    if (idx + 1 === questionList.length) {
      console.log("");
    }
  }; // 

  console.log(counts);

  return (
    <Box
      wordBreak="break-word"
      textAlign="center"
      display="flex"
      justifyContent="center"
      alignItems="center"
    >
      {page <= questionList.length ? (
        <Box>
          <Box fontSize="20px" fontWeight={700}>
            <Box>{`${page} / ${questionList.length}`}</Box>
          </Box>
          {questionList.map((val, idx) => (
            <Box
              display="flex"
              flexDirection="column"
              style={{ display: page === idx + 1 ? "flex" : "none" }}
              key={idx}
            >
              <Box marginTop="30px" marginBottom="30px">
                <Box
                  width="90%"
                  margin="0 auto 30px auto"
                  fontSize="18px"
                  fontWeight={700}
                  color={colorMode === "light" ? "teal.900" : "FFFFFF"}
                >
                  {val.q}
                </Box>
              </Box>
              <Box>
                <Box>
                  {val.a.map((aval, aidx) => (
                    <Button
                      padding="30px"
                      width="90%"
                      marginBottom="20px"
                      colorScheme="teal"
                      variant="outline"
                      key={aidx}
                      onClick={() => handleClickAnswer(aval.score, aidx)}
                    >
                      {aval.text}
                    </Button>
                  ))}
                </Box>
              </Box>
            </Box>
          ))}
        </Box>
      ) : (
        <Result counts={counts} name={name} />
      )}
    </Box>
  );
};
