import {
  Box,
  Button,
  Image,
  Img,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Text,
  useDisclosure,
  VStack,
} from "@chakra-ui/react";
import { useEffect, useState } from "react";

export const resultbox = [
  {
    rank: "0~25%",
    title: "<>",
    img: "https://ifh.cc/g/SKkl4A.jpg",
    text: "    !      ,        .    ,      !",
  },
  {
    rank: "25%~50%",
    title: "<>",
    img: "https://ifh.cc/g/rox40h.jpg",
    text: "   ,      .         !      !",
  },
  {
    rank: "50%~75%",
    title: "<>",
    img: "https://ifh.cc/g/rQn53N.jpg",
    text: ",    !   .     .    ?",
  },
  {
    rank: "75%~100%",
    title: "<>",
    img: "https://ifh.cc/g/6BvMdr.jpg",
    text: "!   .    .     ,     .",
  },
];

export const Result = ({ counts, name }) => {
  const { isOpen, onClose, onOpen } = useDisclosure();

  const [contents, setContents] = useState([]);

  const resultValue = () => {
    if (counts.score <= 7) {
      setContents(resultbox[0]);
    } else if (counts.score <= 15) {
      setContents(resultbox[1]);
    } else if (counts.score <= 23) {
      setContents(resultbox[2]);
    } else if (counts.score <= 30) {
      setContents(resultbox[3]);
    } else {
      // console.log("");
    }
  };

  useEffect(() => {
    resultValue();
  }, [contents]);

  console.log(counts.score);

  return (
    <VStack>
      <Text fontWeight="700" fontSize="20px">
        {name}  ?
      </Text>
      <Box opacity="0.8">{contents.rank}</Box>
      <Box fontSize="20px">  {contents.title}</Box>
      <Box>
        <Image src={contents.img} margin="10px 0"></Image>
      </Box>
      <Box margin="20px" lineHeight="26px">
        {contents.text}
      </Box>
      <Box margin="0 30px 20px 30px" fontSize="14px" opacity="0.7">
               
        <br />     .
      </Box>
      <Button
        onClick={() => window.location.reload()}
        w="250px"
        margin="5px 0"
        padding="20px"
        colorScheme="teal"
        variant="outline"
      >
        
      </Button>
      <Button
        onClick={onOpen}
        w="250px"
        padding="20px"
        colorScheme="teal"
        variant="outline"
      >
          
      </Button>
      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>   </ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <VStack>
              <Text lineHeight="26px">
                <Text fontSize="18px" fontWeight="700" marginBottom="10px">
                   
                </Text>
                1.   10  .
                <br /> 2.           
                 .
                <br />
                3.     .
                <br /> 4.      , 
                  .
                <br /> 5.         
                .
                <br /> <br />   ,  10    
                . <br />
                <br />{" "}
                <Text fontSize="18px" fontWeight="700" marginBottom="10px">
                   
                </Text>
                1.       7
                   . <br />
                <br />    ,   
                .
                <br />  2      
                .
                <br /> <br />
                <Text fontSize="18px" fontWeight="700" marginBottom="10px">
                  
                </Text>
                       
                .
                <br />
                <br />
                1.          
                      .
                <br /> 2.    .
                <br /> <br />
                    .
                <br />
                      
                .
                <br />       
                       
                .
                <br /> <br />
                <Button
                  margin="20px 0"
                  width="100%"
                  onClick={onClose}
                  colorScheme="teal"
                  variant="outline"
                >
                  
                </Button>
              </Text>
            </VStack>
          </ModalBody>
        </ModalContent>
      </Modal>
    </VStack>
  );
};
